import React, { Component } from 'react';
import './App.scss';
// import * as firebase from 'firebase';
import { BrowserRouter as Router, Route, Link,Redirect } from "react-router-dom";
import Navigation from './nav/Navigation.jsx';
import SignIn from './SignIn.jsx';
import SignUp from './SignUp.jsx';
import Dashboard from './Dashboard.jsx';
import Account from './Account.jsx';
import LandingPage from './LandingPage.jsx';
import { firebase } from '../firebase';
import * as routes from '../constants/routes';

const PrivateRoute = ({ component: Component, ...rest }) => (
  <Route {...rest} render={(props) => (
    props.authUser
    ? <Component {...props} />
    : <Redirect to='/signin' />
  )} />
)

function AuthRoutes(props){
  const authUser = props.authUser;
  let routes_el;
  routes_el = <React.Fragment>
                <PrivateRoute
                  exact path={routes.DASHBOARD}
                  component={Dashboard}
                  authUser={authUser}
                />
                <PrivateRoute
                  exact path={routes.ACCOUNT}
                  component={Account}
                  authUser={authUser}
                />
                <Route
                  exact path={routes.SIGN_IN}
                  component={SignIn}
                  authUser={authUser}
                />
                <Route
                  exact path={routes.SIGN_UP}
                  component={SignUp}
                  authUser={authUser}
                />
            </React.Fragment>         
  return routes_el;
}



class App extends Component {

  constructor(){
    super();
    this.state = {
      speed: 10,
      authUser: null,
      isOpen: false
    }
  }

  toggleOpen = () => {
    this.setState({ 
      isOpen: !this.state.isOpen
    });
  }

  
  
  componentDidMount(){
    // const speedRef = firebase.database().ref().child('speed');
    // // this.setState({ speed: 20});
    // // speedRef.set({
    // //   speed: this.state.speed
    // // })
    // speedRef.on('value', snap => {
    //   this.setState({
    //     speed: snap.val()
    //   })
    // });

    firebase.auth.onAuthStateChanged(authUser => {
      authUser
        ? this.setState({ authUser })
        : this.setState({ authUser: null });
    });
  }

  render() {
    const { authUser } = this.state
    return (
      <Router>
        <div className="container-fluid my-pad">
          {/* <div className={`side-menu ${isOpen ? 'active-side-menu' : ''} `}>
            <ul>
              <li>About</li>
            </ul>
          </div> */}
          <Navigation authUser={authUser} onClickMenu={this.toggleOpen}/>
          <AuthRoutes authUser={authUser}/>
          
          {/* redirect / to home */}
          <Route exact path="/" render={() => (
            <Redirect to="/home"/> 
          )}/>
          <Route
            exact path={routes.LANDING}
            component={LandingPage}
          />
        </div>
      </Router>
    );
  }
}

export default App;
