function CheckOpen(props){
  const isOpen = props.open
  const onClickMenu = props.onClickMenu
  let el;
  if( isOpen ){
    el =  <React.Fragment>
            <Close onClick={onClickMenu}/>
          </React.Fragment>
  }else{
    el =  <React.Fragment>
            <Menu className="pointer" onClick={onClickMenu}/>
          </React.Fragment>
  }
  return el;
}

class Navigation extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      isOpen: false
    }
  }

  componentWillMount() {
    // add event listener for clicks
    document.addEventListener('click', this.handleClick, false);
  }

  componentWillUnmount() {
    // make sure you remove the listener when the component is destroyed
    document.removeEventListener('click', this.handleClick, false);
  }

  // fat arrow function binds 'this' to class when called by the listener
  // otherwise you'd need 'this.handleClick.bind(this)' in the constructor
  handleClick = e => {
    // this is the key part - ReactDOM.findDOMNode(this) gives you a reference
    // to your CalendarPopup component;
    // e.target is the element which was clicked upon.
    // check whether the element clicked upon is in your component - if not,
    // then call the close logic
    if(!ReactDOM.findDOMNode(this).contains(e.target)) {
      // the click was outside your component, so handle closing here
      this.setState({ 
        isOpen: false
      });
    }else{
      this.setState({ 
        isOpen: false
      });
    }
  }

  handleOpen = (event, value) => {
    this.setState({ 
      isOpen: !this.state.isOpen
     });
  };

  render() {
    const { authUser, onClickMenu } = this.props
    const { isOpen } = this.state
    return (
      <React.Fragment>
        <div className={ `${isOpen ? 'dark' : ''}`}></div>
        <div className={`side-menu ${isOpen ? 'active-side-menu' : ''} `}>
            <ul>
              <li>About</li>
            </ul>
          </div>
        <nav className="navbar navbar-expand-lg">
          <CheckOpen open={isOpen} onClickMenu={this.handleOpen}/>
          <div className="collapse navbar-collapse" id="navbarNav">
            { authUser
              ? <NavigationAuth Email={authUser.email} routePath={this.props.location.pathname}/>
              : <NavigationNonAuth routePath={this.props.location.pathname}/>
            }
          </div>
        </nav>
      </React.Fragment>
    );
  }
}


