const NavigationAuth = ({ Email, routePath }) =>
  <ul className="navbar-nav">
    <li className="nav-item">
      <Link className={`navbar-link ${routePath == '/dashboard' ? 'activeLink' : ''}`} to={routes.DASHBOARD}>Dashboard</Link>
    </li>
    <li className="nav-item">
      <Link className={`navbar-link ${routePath == '/account' ? 'activeLink' : ''}`} to={routes.ACCOUNT}>Account</Link>
    </li>
    <li className="nav-item">
      <SignOutButton />
    </li>
  </ul>

const NavigationNonAuth = ({ routePath }) =>
  <ul className="navbar-nav">
    <li className="nav-item"><Link className={`navbar-link ${routePath == '/home' ? 'activeLink' : ''}`} to={routes.LANDING}>Home</Link></li>
    <li className="nav-item"><Link className={`navbar-link ${routePath == '/signin' ? 'activeLink' : ''}`} to={routes.SIGN_IN}>Sign In</Link></li>
    <li className="nav-item"><Link className={`navbar-link ${routePath == '/signup' ? 'activeLink' : ''}`} to={routes.SIGN_UP}>Sign Up</Link></li>
  </ul>

export default withRouter(Navigation);